/**
 * Component for displaying basic info about the provided athlete. 
 */

import React from 'react'
import PropTypes from 'prop-types'
import './index.styl'
import {isBestSkill, isWorstSkill} from '../../libs/calculate'
/*
import upperBody from './upperBody.png';
import lowerBody from './lowerBody.png';
import aesthetics from './aesthetics.png';
import agility from './agility.png';
import endurance from './endurance.png';*/
export default class Overview extends React.Component {
    
componentDidMount() {if (this.props.focus=='Overview') {window.location.hash ='Overview'}};


    render() {
        const ContainerFor ={
            MainSection:(props)=> {return (<section id='Overview' className=' l-section c-overview '>{props.children}</section>)},
            Content:(props)=>{return (<div className='content'>{props.children}</div>)},
            Skills:(props)=>{return (<div className=' header-flexible flex-row border-secondary rounded m-1 p-1 shadow-lg p-3  bg-white rounded '>{props.children}</div>)},
        }
        
const Header=()=>{return(<h2 className="header d-flex align-items-center justify-content-center" ><span>Overview</span></h2>)}
//let obj={upperBody:upperBody, lowerBody:lowerBody, aesthetics:aesthetics, agility:agility, endurance:endurance}
const skills= this.props.skillset;
/*
const images={
endurance:"https://i.imgur.com/UwDpicv.png",
lowerBody:"https://i.imgur.com/DsQEi9z.png", 
upperBody:"https://i.imgur.com/zQhXDaW.png",
aesthetics:"https://i.imgur.com/t9SDP2f.png",
agility:"https://i.imgur.com/subifsI.png"
}

*/
const images={
    endurance: require('../../assets/endurance.png'),
    lowerBody: require('../../assets/lowerbody.png'), 
    upperBody: require('../../assets/upperBody.png'),
    aesthetics: require('../../assets/aesthetics.png'),
    agility: require('../../assets/agility.png')
    }
    
    
    




return this.props.isVisible? (
<ContainerFor.MainSection>
    <Header />
  
    <ContainerFor.Content>
        <span className="label">Bio</span>
        <p className="bio">{this.props.bio}</p>
        <span className="label">Skillset</span>
        <ContainerFor.Skills>
            {Object.keys(skills).map((skill) => { const colorclassforBest = isBestSkill(skills[skill],skills)? 'greeen':''; const colorclassforWorst
            =isWorstSkill(skills[skill],skills)? 'worst-color':'' ; return (
            <div className='list' key={skill} tabIndex="0" data-toggle="tooltip" data-placement="bottom" role="tooltip" title={`${skill}`}>
                <div className={` p-2 rounded ${colorclassforBest} ${colorclassforWorst} `}>
                    <img className={ `icon-size` } src={images[skill]}/>
                    <span className='label'> {`: ${skills[skill]}`}</span>
                </div>
            </div>
            ) })}
        </ContainerFor.Skills>
    </ContainerFor.Content>
</ContainerFor.MainSection>
         ):null
    }
}

Overview.propTypes = {
    name: PropTypes.string.isRequired,
    photo: PropTypes.string.isRequired,
    bio: PropTypes.string.isRequired,
    skillset: PropTypes.objectOf(PropTypes.number).isRequired,
    nativeDisciplines: PropTypes.arrayOf(PropTypes.string).isRequired,
}
